.login{
    width: 100vw;
    height: 100vh;
    background-color: #f0f2f5;
    display: flex;
    align-items: center;
    justify-content: center;
    .loginWrapper{
        height: 70%;
        width: 70%;
        display: flex;
        gap: 20px;
        .loginLeft, .loginRight{
            flex:1;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
        
        .loginLeft{
            .loginLogo{
                font-size: 50px;
                font-weight: 800;
                color: #1775ee;
                margin-bottom: 10px;
            }
            .loginDesc{
                font-style: 24px;
            }
        }

        .loginRight{
            .loginBox{
                height: 300px;
                padding: 20px;
                background-color: white;
                border-radius: 10px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                gap: 10px;
                .loginInput{
                    height: 50px;
                    border-radius: 10px;
                    border: 1px solid gray;
                    font-size: 18px;
                    padding-left: 20px;
                    &:focus{
                        outline: 0;
                    }
                }

                .loginBtn{
                    height: 50px;
                    border-radius: 10px;
                    border: 0;
                    background-color: #1775ee;
                    color: white;
                    font-size: 20px;
                    font-weight: 500;
                    cursor: pointer;
                    
                    &.connect{
                        background-color: rgb(172, 172, 172);
                    }
                }

                .loginForgot{
                    text-align: center;
                    color: #1775ee;
                }

                .loginRegisterBtn{
                    height: 50px;
                    align-self: center;
                    width: 60%;
                    border-radius: 10px;
                    border: 0;
                    background-color: #79cd5d;
                    color: white;
                    font-size: 20px;
                    font-weight: 500;
                }
            }
        }
    }
}

