.findFriends{
    display: grid;
    grid-template-columns: 2fr 6.5fr 2.5fr ;
    // width: 100%;
    // gap: 5px;
    
    .friendList{
        
        // height: 100vh;
        display: flex;
        flex-direction: column;
        // gap: 50px;
        // padding: 10px;
        grid-column: 2;
        // grid-row: 2;
        width: 100%;
        // padding: 10px;
        position: relative;
        h1{
            color: rgb(177, 177, 177);
            margin-bottom: 10px;
            margin-top: 10px;
            margin-left: 10px;
            // position:fixed;
            // top: 0;
        }
        .findUser{
            // margin-bottom: 10px;

            padding-bottom: 10px;
            padding-top: 10px;
            margin-left: 10px;
            // padding-top: 10px;
            gap: 10px;
            display: flex;
            border-bottom: 1px solid rgb(237, 237, 237);
            .details{
                flex: 5;
                .userDetails{
                    display: flex;
                    flex-direction: column;
                    gap: 5px;
                    flex: 5;
                    color: rgb(97, 97, 97);
                    font-size: 14px;
                    .nameLink{
                        &:link{
                            text-decoration: 0;
                        }
                        &:hover{
                            text-decoration: underline;
                        }
                        .username{
                            font-size: 16px;
                            font-weight: 800;
                            color: rgb(33, 105, 141);
                            
                        }
                    }
                    .icon{
                        font-size: 17px;
                        margin-right: 5px;
                        transform: translateY(10%);
                    }
                }
                .userBtns{
                    align-self: center;
                    flex:1;
                    button{
                        // margin-top: 30px;
                        margin-bottom: 10px;
                        border: 0;
                        background-color: #1b6de1;
                        color: white;
                        border-radius: 5px;
                        padding: 5px 10px;
                        display: flex;
                        align-items: center;
                        cursor: pointer;
                        font-size: 16px;
                        font-weight: 500;
                        &:hover{
                            background-color: #5b9cf6;
                        }
                        &:focus{
                            outline: 0;
                        }
                    }
                }
                display: flex;
                justify-content: space-between;
            }
            .userImg{
                flex: 1;
                width: 100px;
                height: 100px;
                object-fit: cover;
                object-position: center;
                // border-radius: 20px;
            }
        }
    }

    @media (max-width: 1100px){
        .friendList{
            grid-column:2/-1;
            grid-row: 2;
        }
        .sidebar{
            display: block;
            button{
                display: 'none';
            }
        }

        .rightbar{
            display: none;
        }
    }
    @media (max-width: 1080px){
        // display: none;
        .friendList{
            grid-column:1/-1;
            grid-row: 2;
        }
        .sidebar{
            transform: translateX(-100%);
            transition: .6s;
            &.shown{
                
                transform: translateX(0%);
                transition: .6s;
            }
        }
    }

}